import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import * as Msal from "msal";
var replyUrl = window.location.protocol + "//" + window.location.hostname + (window.location.port !== 80 && window.location.port !== 443 ? ":"+window.location.port : "")

var msalConfig = {
    auth: {
      clientId: "601356a1-0fb8-4920-965a-4ebfcb73c7e2",
      redirectUri: replyUrl,
      authority:"https://login.microsoftonline.com/79dc228f-c8f2-4016-8bf0-b990b6c72e98"
    }
  };
  var requestObj = {
    scopes: ["user.read","Files.ReadWrite.All"]
  };
  var msalInstance = new Msal.UserAgentApplication(msalConfig);
  msalInstance.handleRedirectCallback((error, response) => {
    // handle redirect response or error
  });
  var PTO365 = {
    user: msalInstance.getAccount()
  };
  if (!PTO365.user) {
    msalInstance.loginRedirect(requestObj);
  }
  console.log("User account", PTO365.user);
  

ReactDOM.render(<App />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
